.fade-appear {
  opacity: 0;
}
.fade-appear-active {
  opacity: 1;
  transition: all 750ms;
}
.fade-enter {
  opacity: 0;
  right: -100%;
}
.fade-enter-active {
  opacity: 1;
  right: 0%;
  transition: all 750ms;
}
.fade-exit {
  opacity: 1;
  right: 0%;
  // z-index: -1;
}
.fade-exit-active {
  opacity: 0;
  right: 100%;
  // z-index: -1;
  transition: all 750ms;
}

.fade1-appear {
  opacity: 0;
}
.fade1-appear-active {
  opacity: 1;
  transition: all 350ms;
}
.fade1-enter {
  opacity: 0;
  right: -100%;
}
.fade1-enter-active {
  opacity: 1;
  right: 0%;
  transition: all 350ms;
}
.fade1-exit {
  opacity: 1;
  right: 0%;
}
.fade1-exit-active {
  opacity: 0;
  right: 100%;
  transition: all 350ms;
}
